<template>
    <div>
        <h2>{{_id? '编辑':'新建'}}英雄</h2>
        <el-form label-width='120px' >
            <el-tabs value='second' type="border-card">
                <el-tab-pane label="基本信息" name="first">
                    <el-form-item label='英雄' >
                        <el-input v-model="model.name"></el-input>
                    </el-form-item>
                    <el-form-item label='头像' >
                        <el-upload
                            class="avatar-uploader"
                            :action="getImageUrl"
                            :headers='setTokenInfo()'
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess">
                                <img v-if="model.avatar" :src="model.avatar" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label='类型' >
                        <el-select v-model="model.categories" placeholder="请选择" multiple>
                            <el-option
                                v-for="item in categories"
                                :key="item._id"
                                :label="item.name"
                                :value="item._id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label='难度' >
                        <el-rate v-model="model.scores.difficult" style="margin-top:.6rem" :max='9' show-score></el-rate>
                    </el-form-item>
                    <el-form-item label='技能' >
                        <el-rate v-model="model.scores.skills" style="margin-top:.6rem" :max='9' show-score></el-rate>
                    </el-form-item>
                    <el-form-item label='攻击' >
                        <el-rate v-model="model.scores.attack" style="margin-top:.6rem" :max='9' show-score></el-rate>
                    </el-form-item>
                    <el-form-item label='生存' >
                        <el-rate v-model="model.scores.survive" style="margin-top:.6rem" :max='9' show-score></el-rate>
                    </el-form-item>
                    <el-form-item label='顺风出装' >
                        <el-select v-model="model.item1" placeholder="请选择" multiple>
                            <el-option
                                v-for="item in items"
                                :key="item._id"
                                :label="item.name"
                                :value="item._id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label='逆风出装' >
                        <el-select v-model="model.item2" placeholder="请选择" multiple>
                            <el-option
                                v-for="item in items"
                                :key="item._id"
                                :label="item.name"
                                :value="item._id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label='使用技巧' >
                        <el-input type="textarea" v-model="model.usageTips"></el-input>
                    </el-form-item>
                    <el-form-item label='对抗技巧' >
                        <el-input type="textarea" v-model="model.battleTips"></el-input>
                    </el-form-item>
                    <el-form-item label='团战思路' >
                        <el-input type="textarea" v-model="model.teamTips"></el-input>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="技能配置" name="second">
                    <el-button size='small' @click="model.skills.push({})">
                        <i class="el-icon-plus"></i>添加
                    </el-button>
                    <el-row type='flex' style="flex-wrap:wrap">
                        <el-col :md='12' v-for="(item,index) in model.skills" :key='index'>
                            <el-form-item label='名称' >
                                <el-input v-model="item.name"></el-input>
                            </el-form-item>
                            <el-form-item label='图标' >
                                <el-upload
                                    class="avatar-uploader"
                                    :action="getImageUrl"
                                    :headers='setTokenInfo()'
                                    :show-file-list="false"
                                    :on-success="res => $set(item,'icon',res.url)">
                                    <img v-if="item.icon" :src="item.icon" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </el-form-item>
                            <el-form-item label='冷却值' >
                                <el-input v-model="item.delay"></el-input>
                            </el-form-item>
                            <el-form-item label='消耗' >
                                <el-input v-model="item.cost"></el-input>
                            </el-form-item>
                             <el-form-item label='描述' >
                                <el-input v-model="item.description"></el-input>
                            </el-form-item>
                             <el-form-item label='小提示' >
                                <el-input v-model="item.tips"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button size='small' type='danger' @click="model.skills.splice(index,1)">删除</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
            <el-form-item style="margin-top:3rem">
                <el-button type='primary' @click="save">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: "HerosEdit",
        data() {
            return{
                model: {
                    scores:{},
                    skills:[]
                },
                categories:[],
                items: []
            }
        },
        props: ['_id'],
        created() {
            this._id && this.fatch();
            this.fatchCategories();
            this.fatchItems();
        },
        methods: {
            async save(){
                if(this._id){ //修改数据
                    await this.$http.put(`/rest/heros/${this._id}`,this.model)
                }else{ //创建数据
                    let res = await this.$http.post('/rest/heros',this.model);
                }
                this.$router.push('/heros/list');
                this.$message({
                    type: 'success',
                    message: '保存成功'
                })
            },
            async fatch(){ //获取单条数据
                let res = await this.$http.get(`/rest/heros/${this._id}`)
                this.model = Object.assign({},this.model,res.data);
            },
            handleAvatarSuccess(res) { //图片上传成功
                this.$set(this.model,'avatar',res.url);
            },
            async fatchCategories(){
                let res = await this.$http.get('/rest/categories')
                this.categories = res.data;
            },
             async fatchItems(){
                let res = await this.$http.get('/rest/items')
                this.items = res.data;
            },
        },
    }
</script>

<style >
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
border-color: #409EFF;
}
.avatar-uploader-icon {
font-size: 28px;
color: #8c939d;
width: 178px;
height: 178px;
line-height: 178px;
text-align: center;
}
.avatar {
width: 178px;
height: 178px;
display: block;
}
</style>